/**
 * 属性详情
 */
export class PropertyConfigItem {
    /**
     * 属性ID，取值应为BPMN活动字段名
     */
    propertyId: string;
    /**
     * 属性名称
     */
    propertyName: string;
    /**
     * 属性类型
     */
    propertyType: "string" | "number" | "boolean" | "select" | "modal" = "string";
    /**
     * 属性值
     */
    propertyValue: any;
    /**
     * 是否只读
     */
    readonly?: boolean = false;
    /**
     * 是否可见
     */
    visible?: boolean = true;
    /**
     * 属性描述
     */
    description?: string;

    constructor(propertyId?: string, propertyName?: string, propertyType?: "string" | "number" | "boolean" | "select" | "modal", propertyValue?: any) {
        this.propertyId = propertyId;
        this.propertyName = propertyName;
        this.propertyType = propertyType;
        this.propertyValue = propertyValue;
    }
}